import Install from './docs/install.md'
import QuikeStart from './docs/quikeStart.md'
import Button from './docs/button.md'
import Checkbox from './docs/checkbox.md'
import CascadeTree from './docs/cascade-tree.md'
const routes = [
  {
    path: '/',
    component: Install,
    name: 'default'
  },
  {
    path: '/guide/install',
    name: 'Install',
    component: Install
  },
  {
    path: '/guide/quikeStart',
    name: 'quikeStart',
    component: QuikeStart
  },
  {
    path: '/button',
    name: 'button',
    component: Button
  },
  {
    path: '/checkbox',
    name: 'checkbox',
    component: Checkbox
  },
  {
    path: '/cascadeTree',
    name: 'cascadeTree',
    component: CascadeTree
  }
]

export default routes



